/* --- Buttons --- */

.AllButton {
  background-color: #e5e5e5;
  color: white;
  border-width: 0;
  border-radius: 10px;
  width: 100%;
  margin: 5px 0px;
  padding: 12px 14px;
  text-align: left;
  min-height: 50px;
  height: fit-content;
}

.Choice {
  background-color: #4e3629;
  color: white;
  transition-duration: 0.4s;
}

.Choice:hover {
  background-color: #261a14; /* same as website */
  cursor: pointer;
}

.ButtonGroup {
  position: relative;
}

.ChoiceBear {
  width: 35px !important;
  height: 35px !important;
  /* margin: 0 !important; */
  /* padding-right: 5px; */
  margin: 0 !important;
  position: absolute;
  /* top: 80px; */
  right: 15px;
  bottom: 12px;
  z-index: 999;
  border-radius: 0 !important;
}

.GreatJobBear {
  width: 35px !important;
  height: 35px !important;
  margin: 0 !important;
  border-radius: 0 !important;
}

.Correct {
  background-color: #54c157 !important;
}

.IncorrectCorrect {
  border: solid #54c157;
  border-width: 2px;
  color: rgb(88, 88, 88);
}

.Incorrect {
  background-color: #EC2027 !important;
}

.Other {
  color: rgb(88, 88, 88);
}

/* --- Question Group CSS --- */

.Question {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  margin: 0 auto;
  width: 75%;
  max-width: 600px;
  position: relative;
}

.Question img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  margin-bottom: 20px;
}

.num {
  position: absolute;
  top: 1px;
  left: calc(-35px - 0.01vw);
}

.Options {
  margin-top: 15px;
  width: 100%;
}

/* --- More info CSS --- */

.MoreInfo {
  text-align: right;
  width: 100%;
  /* background-color: blue; */
}

.MoreInfo p {
  text-align: left;
  padding: 0 0 10px 15px;
  margin: 16px 0 8px 0;
  width: 90%;
  font-size: calc(16px + 0.3vw);
  color: rgb(36, 36, 36);
}

.MoreInfo a {
  text-decoration: none;
  color: inherit;
  transition-duration: 0.4s;
  line-height: calc(18.5px + 0.5vw);
  font-weight: 700;
  font-family: "freight-text-pro", "Georgia", "Times", serif;
  font-style: normal;
  font-size: calc(18px + 0.3vw);
}

.MoreInfo a:hover {
  font-size: calc(19px + 0.3vw);
  font-weight: 700;
}

/* fonts */

/* questions */
h4 {
  font-family: "freight-text-pro", "Georgia", "Times", serif;
  font-weight: 700;
  margin: 0;
  padding: 0 0 0 5px;
  font-size: calc(20px + 0.5vw);
}

/* other */
button,
p {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  text-align: left;
  font-size: calc(14px + 0.3vw);
  transition-duration: 0;
}

hr {
  margin: min(10%, 50px) auto;
  width: 75%;
  max-width: 600px;
}

#logo {
  /* width: calc(300px + 35%); */
  /* width: calc(300px + 27.5%); */
  width: 30%;
  max-width: 730px;
  margin: 0 auto;
  display: block;
  padding-top: 20px;
}

.score {
  text-align: center;
}

.Main {
  padding-bottom: 50px;
}

#intro {
  text-align: center;
  margin-top: 2vh;
}

#date {
  text-align: center;
  margin: 4px;
  margin-top: 6px;
  font-family: "freight-text-pro", "Georgia", "Times", serif;
  font-weight: 400;
  font-size: calc(16px + 0.3vw);
  font-style: normal;
}

#me {
  text-align: center;
  margin: 4px;
  padding: 0;
  margin-bottom: 4vh;
  font-family: "freight-text-pro", "Georgia", "Times", serif;
  font-weight: 400;
  font-size: calc(16px + 0.3vw);
  font-style: normal;
}

.Top {
  text-align: center;
  font-family: "freight-text-pro", "Georgia", "Times", serif !important;
  font-weight: 400;
  font-style: normal;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.SliderBlock {
  display: flex;
  width: 100%;
  padding: 15px 0px;
}

.Slider {
  margin-right: 20px;
}

.SliderQ .AllButton {
  width: 100px;
  text-align: center;
}

.Load {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}


@media (prefers-color-scheme: dark) {
  body {
    background-color: #121212;
    color: #e5e5e5;
  }

  .Other {
    color: #e5e5e5;
  }

  .MoreInfo p {
    color: #e5e5e5;
  }

  .MoreInfo a {
    color: #e5e5e5;
  }

  h4 {
    color: #e5e5e5;
  }

  button,
  p {
    color: #e5e5e5;
  }

  #date,
  #me {
    color: #e5e5e5;
  }

  .Top {
    color: #e5e5e5;
  }
}